* {
  margin: 0;
  padding: 0;
}

.container {
  width: 1140px;
  margin: 0 auto;
}

.section {
  width: 100%;
  float: left;
  clear: both;
}

body {
  overflow-x: hidden;
}

.topPanel {
  border-color: #fff;
  padding: 10px 0 5px;
}

.topPanel .navbar {
  float: left;
}

.topPanel .navbar ul {
  list-style: none;
}

.topPanel .navbar li {
  float: left;
}

.topPanel .navbar li a {
  font-weight: 300;
  font-size: 14px;
  font-family: 'Roboto', sans-serif;
  color: #374470;
  text-decoration: none;
  padding-right: 20px;
}

.topPanel .navbar li a:hover {
  text-decoration: underline;
}

.topPanel .social {
  float: left;
}

.topPanel .social a {
  background-color: #34476b;
  width: 22px;
  height: 22px;
  display: flex;
  align-items: center;
  justify-content: space-around;
  border-radius: 100%;
  float: left;
  margin: 0 5px;
}

.topPanel .callBack {
  float: right;
  display: flex;
  align-items: center;
}

.topPanel .callBack a, .topPanel .callBack div {
  float: left;
}

.topPanel .callBack a {
  font-family: 'Roboto', sans-serif;
  color: #666666;
  font-size: 18px;
  text-decoration: none;
  display: block;
  padding-left: 30px;
  background-image: url("../images/icons/call.png");
  background-repeat: no-repeat;
  background-position: left center;
}

.topPanel .callBack a:hover {
  text-decoration: underline;
}

.topPanel .callBack .keyCallBack {
  font-family: 'Roboto' , sans-serif;
  font-size: 14px;
  font-weight: 300;
  color: #ae1c51;
  margin-left: 15px;
  cursor: pointer;
}

.topPanel .callBack .keyCallBack span {
  border-bottom: 1px dotted #ae1c51;
}

.searchTop {
  padding: 10px 0 20px;
  border-bottom: 1px solid #e9e9e9;
}

.searchTop .logo {
  float: left;
  margin-right: 30px;
  margin-top: 5px;
}

.searchTop .searchField {
  position: relative;
  display: block;
  float: left;
}

.searchTop .searchField .textField {
  border: 1px solid #e9e9e9;
  box-sizing: border-box;
  height: 50px;
  width: 720px;
  padding: 5px;
  display: block;
}

.searchTop .searchField .sendField {
  position: absolute;
  right: 0;
  top: 15px;
  right: 15px;
}

.searchTop .empty {
  border: 1px solid #ac1c57;
  color: #ac1c57;
  display: flex;
  align-items: center;
  justify-content: space-around;
  height: 50px;
  box-sizing: border-box;
  float: right;
  width: 190px;
  font-family: 'Roboto' , sans-serif;
  font-size: 14px;
  font-weight: 300;
}

.categoryNav {
  width: 100%;
  border-top: 1px solid #e9e9e9;
  border-bottom: 1px solid #e9e9e9;
}

.categoryNav .navbar {
  position: relative;
  z-index: 10;
}

.categoryNav .navbar > ul {
  display: flex;
  justify-content: space-between;
}

.categoryNav .navbar > ul > li {
  float: left;
  list-style: none;
  padding: 25px 15px;
}

.categoryNav .navbar > ul > li[data-status="hidden"] .navContent {
  display: none;
}

.categoryNav .navbar > ul > li[data-status="visible"] {
  border-right: 1px solid #e9e9e9;
  border-left: 1px solid #e9e9e9;
  border-bottom: 1px solid #fff;
  z-index: 2;
}

.categoryNav .navbar > ul > li[data-status="visible"] .navContent {
  z-index: 500;
  display: block;
  border: 1px solid #e9e9e9;
}

.categoryNav .navbar span {
  text-decoration: none;
  font-family: 'Roboto' , sans-serif;
  font-size: 16px;
  font-weight: 300;
  color: #666666;
  background-image: url("../images/icons/arrowBottom.png");
  background-repeat: no-repeat;
  background-position: right center;
  padding-right: 35px;
  cursor: pointer;
}

.categoryNav .navbar span:hover {
  color: #b01b52;
}

.categoryNav .navContent {
  position: absolute;
  top: 70px;
  width: 1140px;
  max-width: 100%;
  background-color: #fff;
  z-index: 999;
  left: 0;
  padding: 15px 20px 55px;
  height: 175px;
  box-sizing: border-box;
  float: left;
  overflow: hidden;
}

.categoryNav .navContent > ul > li > a {
  text-transform: uppercase;
  display: block;
  margin-bottom: 13px;
}

.categoryNav .navContent li {
  list-style: none;
  margin: 7px 0;
  display: block;
}

.categoryNav .navContent li[data-more="true"] {
  float: left;
}

.categoryNav .navContent li[data-more="false"] {
  padding-top: 5px;
}

.categoryNav .navContent li > ul.col-2 {
  width: 350px;
  display: flex;
  flex-wrap: wrap;
}

.categoryNav .navContent li > ul.col-2 > li {
  float: left;
  margin-right: 90px;
  width: 80px;
}

.categoryNav .navContent li > ul.col-1 {
  column-count: 1;
}

.categoryNav .navContent li > ul.col-1 > li {
  float: none;
  margin-right: 90px;
  width: 80px;
}

.categoryNav .navContent li a {
  list-style: none;
  font-family: 'Roboto' , sans-serif;
  font-size: 14px;
  color: #000;
  text-decoration: none;
}

.categoryNav .navContent li a:hover {
  text-decoration: underline;
}

.categoryNav .navContent .actionBanner {
  position: absolute;
  top: 0;
  right: 0;
}

.footer {
  padding: 40px 0;
  background-color: #eeeeee;
}

.footer .container > div {
  float: left;
  box-sizing: border-box;
}

.footer .container > div .title {
  font-family: 'Roboto' , sans-serif;
  font-size: 14px;
  font-weight: 700;
  color: #666666;
  margin-bottom: 3px;
  margin-top: 10px;
}

.footer .container > div a {
  text-decoration: none;
  font-family: 'Roboto' , sans-serif;
  font-size: 14px;
  font-weight: 300;
  color: #37466f;
}

.footer .container > div a:hover {
  text-decoration: underline;
}

.footer .container > div li {
  list-style: none;
  padding: 5px 0;
}

.footer .container > div span {
  font-family: 'Roboto' , sans-serif;
  color: #525252;
  font-size: 14px;
  font-weight: 300;
}

.footer .container > div span a {
