}

.footer .sector1 .about {
  padding: 20px 0 15px;
}

.footer .sector1 .fieldGroup .textField {
  width: 160px;
  height: 30px;
  box-sizing: border-box;
  padding: 5px;
  font-family: 'Roboto', sans-serif;
  font-weight: 300;
  color: #333;
  border: 0;
  display: block;
  float: left;
}

.footer .sector1 .fieldGroup .sendField {
  width: 30px;
  height: 30px;
  box-sizing: border-box;
  border: 1px solid #ae1c51;
  background-color: #fff;
  background-image: url("../images/icons/arrowPodpiska.png");
  background-repeat: no-repeat;
  background-position: center;
  cursor: pointer;
}

.footer .sector2 {
  width: 20.6%;
  padding: 0 50px 0 25px;
}

.footer .sector3 {
  width: 33.3%;
  padding: 0 25px;
}

.footer .sector3 li {
  padding-left: 25px !important;
}

.footer .sector3 .address {
  background-image: url("../images/icons/footerAddress.png");
  background-repeat: no-repeat;
  background-position: left 8px;
}

.footer .sector3 .email {
  background-image: url("../images/icons/footerEmail.png");
  background-repeat: no-repeat;
  background-position: left 8px;
}

.footer .sector3 .skype {
  background-image: url("../images/icons/footerSkype.png");
  background-repeat: no-repeat;
  background-position: 10px 0;
  padding-left: 35px;
}

.footer .sector3 .call {
  background-image: url("../images/icons/call.png");
  background-repeat: no-repeat;
  background-position: left 8px;
}

.footer .sector3 .time {
  background-image: url("../images/icons/footerTimeWork.png");
  background-repeat: no-repeat;
  background-position: left 8px;
}

.footer .sector4 {
  width: 20.1%;
  padding: 0 25px;
}

.footer .sector4 a {
  display: flex;
  align-items: center;
}

.footer .sector4 a img {
  margin-left: 8px;
}

.copyright {
  background-color: #eeeeee;
  padding: 20px 0 15px;
  text-align: center;
  border-top: 1px solid #dedede;
}

.copyright span {
  font-family: 'Roboto' , sans-serif;
  font-size: 12px;
  font-weight: 300;
  color: #999898;
}

.hidden {
  display: none;
}

.animKey {
  transition-duration: .5s;
}

.animKey:hover {
  background-color: #b01b52 !important;
  opacity: 1 !important;
  color: #fff !important;
}

@keyframes dropTop {
  0% {
    margin-top: -100px;
  }
  100% {
    margin-top: 100px;
  }
}

.modalWindow {
  width: 100vw;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.4);
  position: fixed;
  top: 0;
  left: 0;
  z-index: 20;
  display: none;
}

.modalWindow[data-modal-status="visab"] {
  display: block;
}

.modalWindow[data-modal-status="visab"] .contentWindowModal {
  margin-top: 100px;
  animation: dropTop .5s;
}

.modalWindow[data-modal-status="hidden"] {
  display: none;
}

.modalWindow[data-modal-status="hidden"] .contentWindowModal {
  margin-top: -100px;
  animation: dropTop .5s;
}

.modalWindow .close {
  cursor: pointer;
}

.modalWindow .contentWindowModal {
  margin: -100px auto 0 auto;
  animation: dropTop .5s;
  position: relative;
}

.modalWindow .contentWindowModal .close {
  position: absolute;
  bottom: 100%;
  margin-bottom: -15px;
  left: 100%;
  margin-left: -10px;
  background-color: #fff;
  display: flex;
  align-items: center;
  border: 1px solid #ae1c51;
  width: 30px;
  height: 30px;
  background-image: url("../images/icons/rmParamRed.png");
  background-position: center;
  background-repeat: no-repeat;
}

.sizeWindow .contentWindowModal {
  width: 670px;
  max-width: 80%;
  background-color: #fff;
  padding: 30px  15px 15px 15px;
  box-sizing: border-box;
}

.sizeWindow .contentWindowModal .table {
  border: 1px solid #010101;
}

.sizeWindow .contentWindowModal .table .row {
  width: 100%;
  display: flex;
  justify-content: space-around;
  border-bottom: 1px solid #808080;
}

.sizeWindow .contentWindowModal .table .row:last-child {
  border: 0;
}

.sizeWindow .contentWindowModal .table .col {
  width: 33.3%;
  text-align: center;
  border-right: 1px solid #808080;
}

.sizeWindow .contentWindowModal .table .col:last-child {
