}

.obl-carousel .stage {
  padding: 20px 0;
}

.obl-carousel .card {
  position: relative;
}

.obl-carousel .card img {
  width: 100%;
}

.obl-carousel .card:hover {
  margin-top: -10px;
}

.obl-carousel .card:hover .psevdoCard {
  position: absolute;
  padding: 10px;
  width: 100%;
  height: inherit;
  top: 0;
  left: -10px;
  z-index: 1;
  box-shadow: 0 0 15px rgba(0, 0, 0, 0.1);
  background-color: #fff;
}

.obl-carousel .card:hover .size {
  display: block;
}

.obl-carousel .card:hover.new:before, .obl-carousel .card:hover .dropPrice {
  z-index: 5;
  top: 20px;
}

.obl-carousel .card.new:before {
  position: absolute;
  top: 0;
  right: 0;
  content: "NEW";
  font-family: 'Roboto' , sans-serif;
  font-size: 14px;
  color: #fff;
  font-weight: 300;
  background-color: #00838f;
  width: 40px;
  padding: 4px 0;
  text-align: center;
  top: 10px;
}
