// modal window
@keyframes dropTop {
	0% {margin-top: -100px};
	100%{margin-top: 100px}; }
.modalWindow {
	width: 100vw;
	height: 100vh;
	background-color: rgba(0,0,0,.4);
	position: fixed;
	top: 0;
	left: 0;
	z-index: 20;
	display: none;
	&[data-modal-status="visab"] {
		display: block;
		.contentWindowModal {
			margin-top: 100px;
			animation: dropTop .5s; } }
	&[data-modal-status="hidden"] {
		display: none;
		.contentWindowModal {
			margin-top: -100px;
			animation: dropTop .5s; } }
	.close {
		cursor: pointer; }
	.contentWindowModal {
		margin: -100px auto 0 auto;
		animation: dropTop .5s;
		position: relative;
		.close {
			position: absolute;
			bottom: 100%;
			margin-bottom: -15px;
			left: 100%;
			margin-left: -10px;
			background-color: #fff;
			display: flex;
			align-items: center;
			border: 1px solid #ae1c51;
			width: 30px;
			height: 30px;
			background-image: url('../images/icons/rmParamRed.png');
			background-position: center;
			background-repeat: no-repeat; } } }
